import React, { useEffect, useState } from 'react'
import Todo_list from './Todo_list';


const getLocalData =()=>{
    const list = localStorage.getItem("todolist")

    if(list){
        return JSON.parse(list);
    }else{
        return [];
    }
}

const Todo = () => {

    const [additem, setItem] = useState();
    const [itemlist, setitemlist] = useState(getLocalData())

    const inputChange = (e)=>{
        setItem(e.target.value)
    }

    const add = ()=>{

        if(!additem){
            alert('Blank value not allow....!')
        }else{
            setitemlist( [...itemlist, additem]);
            setItem("");
        }
        
        
    }

    const deleteitem = (id)=>{
        setitemlist((previtem)=>{
            return previtem.filter((arrEle, index)=>{
                return index !== id
            })
        })

    }

    useEffect(()=>{
        localStorage.setItem('todolist', JSON.stringify(itemlist))
    },[itemlist])
    
    return (
        <div>
            <h1>ToDo App</h1>
            <input type="text" placeholder="📝 add items" onChange={inputChange} value={additem} />
            <button   className="add_btn" onClick ={add}>+</button>
            <ul>
                {
                    itemlist.map((item, index)=>{
                       return <Todo_list key={index} id={index} text={item} onDelete={deleteitem} />
                    })
                }
                
            </ul>
        </div>
    )
}

export default Todo
